import PropTypes from "prop-types";
import i18n from "../../../utils/locale/i18n";
import { FIELD_TYPES } from "../../../constants/field-types";

const ColumnsVisibility = ({
  columns,
  selectColumnsAllOrNone,
  toggleVisibleColumn,
  visibleColumns,
  classes,
}) => {
  return (
    <>
      <div className={classes["tab-content"]}>
        <div>
          <label>
            <input
              type="checkbox"
              onChange={() => selectColumnsAllOrNone(columns)}
              checked={visibleColumns.length === columns.length}
            />
            &nbsp;&nbsp;{i18n.t("select_all")}
          </label>
        </div>
        {columns
          .filter((c) => c.dataType !== FIELD_TYPES.INVISIBLE)
          .map((column) => (
            <div key={column.name}>
              <label>
                <input
                  type="checkbox"
                  checked={visibleColumns.includes(column.name)}
                  onChange={() => toggleVisibleColumn(column.name, columns)}
                />
                &nbsp;&nbsp;
                {column.displayAs}
              </label>
            </div>
          ))}
      </div>
    </>
  );
};

ColumnsVisibility.propTypes = {
  classes: PropTypes.object,
};

export default ColumnsVisibility;
