import PropTypes from "prop-types";
import { createUseStyles } from "react-jss";
import { useState, useRef } from "react";
import classNames from "classnames";

import buttonDropdown from "../skin/buttons/button-dropdown.skin";
import Icon from "../../../components/common/icon";
import { DROPDOWN_DIRECTION } from "../../../constants/dropdown";

const useStyles = createUseStyles(buttonDropdown);

const ButtonDropdown = ({
  buttons = [],
  buttonIcon = "",
  buttonLabel = "",
  direction = DROPDOWN_DIRECTION.LEFT,
  leftSpace = false,
  bottomSpace = false,
}) => {
  const buttonDropdownRef = useRef(null);
  const [opened, setOpen] = useState(false);

  function openToggle() {
    setOpen(!opened);
  }

  function close() {
    // Having enough time to close in case you press any click into the dropdown
    setTimeout(() => {
      setOpen(false);
    }, 200);
  }

  const classes = useStyles();

  if (buttons.length === 0) {
    return null;
  }

  return (
    <div
      className={classNames({
        [classes["dropdown-menu-container"]]: true,
        [classes["left-space"]]: leftSpace,
        [classes["bottom-space"]]: bottomSpace,
      })}
      ref={buttonDropdownRef}
    >
      <button
        className={classes["dropdown-menu-button"]}
        onClick={openToggle}
        onBlur={close}
      >
        {buttonIcon && (
          <>
            <Icon icon={buttonIcon} />
            &nbsp;
          </>
        )}
        {buttonLabel}&nbsp;
        <Icon icon="caret" />
      </button>

      <ul
        className={classNames({
          [classes["dropdown-menu"]]: true,
          [classes["dropdown-menu-right"]]:
            direction === DROPDOWN_DIRECTION.RIGHT,
          [classes["dropdown-menu-opened"]]: opened,
          [classes["dropdown-menu-closed"]]: !opened,
        })}
      >
        {buttons.map((button) => (
          <li key={button.key}>
            <a
              href={button.url ? button.url : ""}
              target={button.newTab ? "_blank" : undefined}
              className="dropdown-item"
              rel="noreferrer"
              onClick={
                button.onClick
                  ? (event) => {
                      event.preventDefault();
                      if (button.onClick) {
                        button.onClick({
                          primaryKeyValue: button.primaryKeyValue,
                        });
                      }
                    }
                  : undefined
              }
            >
              {button.icon && (
                <>
                  <Icon icon={button.icon} />
                  &nbsp;&nbsp;
                </>
              )}
              {button.text}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

ButtonDropdown.propTypes = {
  buttons: PropTypes.array,
  buttonIcon: PropTypes.string,
  buttonLabel: PropTypes.string,
  direction: PropTypes.oneOf([
    DROPDOWN_DIRECTION.LEFT,
    DROPDOWN_DIRECTION.RIGHT,
  ]),
  leftSpace: PropTypes.bool,
};

export default ButtonDropdown;
