import classNames from "classnames";
import { createUseStyles } from "react-jss";
import PropTypes from "prop-types";
import formDialog from "../skin/form-dialog/form-dialog.skin";
import i18n from "../../../utils/locale/i18n";
import DatagridMiniGrid from "../datagrid/datagrid-mini-grid";
import DialogHeader from "../common/dialog-header";

const useStyles = createUseStyles(formDialog);

const DeleteSingleModal = (props) => {
  const {
    deleteOneModalOpen,
    deleteOneModalClose,
    deleteOne,
    visibleColumnsWithDetails,
    primaryKeyValue,
    rows,
  } = props;

  const classes = useStyles();

  return (
    <div
      className={classNames({
        [classes["dialog"]]: true,
        [classes["dialog-open"]]: deleteOneModalOpen,
        [classes["dialog-closed"]]: !deleteOneModalOpen,
      })}
      tabIndex="-1"
      role="dialog"
      aria-labelledby="myModalLabel"
    >
      <div className={classes["dialog-modal"]} role="document">
        <div className={classes["dialog-content"]}>
          <DialogHeader
            title={i18n.t("action_delete")}
            onClose={deleteOneModalClose}
          />
          <div className="modal-body">
            <p>{i18n.t("confirm_delete")}</p>
            {deleteOneModalOpen && (
              <DatagridMiniGrid
                visibleColumns={visibleColumnsWithDetails}
                rows={rows.filter(
                  (row) =>
                    row.grocery_crud_extras.primaryKeyValue === primaryKeyValue
                )}
              />
            )}
          </div>
          {deleteOneModalOpen && (
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-default btn-outline-dark"
                data-dismiss="modal"
                onClick={deleteOneModalClose}
              >
                {i18n.t("cancel")}
              </button>
              <button
                type="button"
                className="btn btn-danger delete-single-confirmation-button"
                onClick={deleteOne}
              >
                {i18n.t("action_delete")}
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

DeleteSingleModal.propTypes = {
  modalLoading: PropTypes.bool,
  formModalOpen: PropTypes.bool,
  formState: PropTypes.string,
};

export default DeleteSingleModal;
