import Icon from "../../../components/common/icon";
import buttonsSkin from "../skin/buttons/buttons.skin";
import { createUseStyles } from "react-jss";
import classNames from "classnames";

const useStyles = createUseStyles(buttonsSkin);

const Button = ({
  onClick,
  icon,
  label,
  link = false,
  href,
  newTab = false,
  bottomSpace,
  className,
}) => {
  const classes = useStyles();

  const buttonClasses = classNames(className, {
    [classes["simple-button"]]: !className,
    [classes["bottom-space"]]: bottomSpace,
  });

  if (link) {
    return (
      <a
        className={buttonClasses}
        onClick={onClick}
        href={href}
        title={label}
        target={newTab ? "_blank" : undefined}
        rel="noreferrer"
      >
        {icon && (
          <>
            <Icon icon={icon}></Icon>&nbsp;
          </>
        )}
        {label}
      </a>
    );
  }

  return (
    <button className={buttonClasses} onClick={onClick} title={label}>
      {icon && (
        <>
          <Icon icon={icon}></Icon>&nbsp;
        </>
      )}
      {label}
    </button>
  );
};

export default Button;
