import { useEffect, useState } from "react";
import { createUseStyles } from "react-jss";
import { useForm } from "react-hook-form";
import { useSelector, useDispatch } from "react-redux";
import PropTypes from "prop-types";

import formDialog from "../skin/form-dialog/form-dialog.skin";
import i18n from "../../../utils/locale/i18n";
import { formSubmitTransformation } from "../../../utils/form-fields";
import InputContainer from "../../../components/common/input-container";
import { formActions } from "../../../actions/form-actions";
import {
  EVENT_SET_FIELD_VALUE,
  subscribeEvent,
  unsubscribeEvent,
} from "../../../utils/events";
import classNames from "classnames";

const selectCloseModalOnSave = (state) => state.form.closeModalOnSave;

const useStyles = createUseStyles(formDialog);

const Form = ({
  configurationSettings = {},
  formFields = [],
  formIsReadOnly: readOnly,
  formLoadingOverlay,
  formModalClose,
  formState,
  loadCssThirdParty,
  modalLoading,
  onFormSubmit,
  publishEvents,
}) => {
  const closeModalOnSave = useSelector(selectCloseModalOnSave);
  const openInModal = useSelector((state) => state.configuration.openInModal);
  const groupFields = useSelector((state) => state.form.groupFields);
  const dispatch = useDispatch();
  const { handleSubmit, control, register, setValue } = useForm();

  const [visibleTab, setVisibleTab] = useState(0);

  useEffect(() => {
    const handleCustomEvent = ({ detail }) => {
      setValue(detail.fieldName, detail.fieldValue);
    };

    if (publishEvents) {
      subscribeEvent(EVENT_SET_FIELD_VALUE, handleCustomEvent);
    }

    // Clean up event listener on component unmount
    return () => {
      if (publishEvents) {
        unsubscribeEvent(EVENT_SET_FIELD_VALUE, handleCustomEvent);
      }
    };
  }, [setValue, publishEvents]);

  const onSubmit = (data) => {
    const transformedData = formSubmitTransformation(data, formFields);

    onFormSubmit({
      formState,
      data: transformedData,
    });
  };
  const onError = (errors, e) => console.log(errors, e);

  const classes = useStyles();

  const hasGroupFields = groupFields.length > 0;

  return (
    <form
      className={classNames({
        [classes["form"]]: true,
        [classes["form-loading"]]: formLoadingOverlay,
      })}
      onSubmit={handleSubmit(onSubmit, onError)}
    >
      <div
        className={classNames({
          [classes["form-fields"]]: true,
          [classes["form-fields-dialog"]]: openInModal,
        })}
      >
        {modalLoading && "Loading..."}
        {!modalLoading && hasGroupFields && (
          <>
            <ul className={classes["tabs"]}>
              {groupFields.map((groupField, groupKey) => (
                <li key={groupKey} className="nav-item">
                  <a
                    className={classNames(classes["tab-button"], {
                      active: groupKey === visibleTab,
                    })}
                    onClick={(event) => {
                      event.preventDefault();
                      setVisibleTab(groupKey);
                    }}
                  >
                    {groupField.displayAs}
                  </a>
                </li>
              ))}
            </ul>
            <div className="tab-content">
              {groupFields.map((groupField, groupKey) => {
                const filteredFields = formFields.filter((field) =>
                  groupField.fields.includes(field.fieldName)
                );

                return (
                  <div
                    className={classNames(classes["tab-content"], {
                      [classes["tab-content-active"]]: groupKey === visibleTab,
                      [classes["tab-content-inactive"]]:
                        groupKey !== visibleTab,
                    })}
                    role="tabpanel"
                    key={groupKey}
                  >
                    {filteredFields.map((field) => {
                      return (
                        <InputContainer
                          control={control}
                          field={field}
                          inputContainerClassName="col-sm-9"
                          jssClasses={classes}
                          key={field.fieldName}
                          labelClassName="col-sm-3 col-form-label"
                          labelContainerClassName="mb-3 row"
                          loadCssThirdParty={loadCssThirdParty}
                          readOnly={readOnly}
                          register={register}
                          setValue={setValue}
                          configurationSettings={configurationSettings}
                          formState={formState}
                        />
                      );
                    })}
                  </div>
                );
              })}
              <div className="tab-pane fade show active" role="tabpanel"></div>
            </div>
          </>
        )}
        {!modalLoading &&
          !hasGroupFields &&
          formFields.map((field) => {
            return (
              <InputContainer
                control={control}
                field={field}
                inputContainerClassName="col-sm-9"
                jssClasses={classes}
                key={field.fieldName}
                labelClassName="col-sm-3 col-form-label"
                labelContainerClassName="mb-3 row"
                loadCssThirdParty={loadCssThirdParty}
                readOnly={readOnly}
                register={register}
                setValue={setValue}
                configurationSettings={configurationSettings}
                formState={formState}
              />
            );
          })}
      </div>
      <div className="modal-footer">
        {!readOnly && (
          <>
            <label className="options-on-save">
              <input
                type="checkbox"
                checked={closeModalOnSave}
                onChange={() =>
                  dispatch({
                    type: formActions.TOGGLE_CLOSE_MODAL_ON_SAVE,
                  })
                }
              />{" "}
              {i18n.t("close_modal_on_save")}
            </label>{" "}
            &nbsp; &nbsp;
          </>
        )}
        <button
          type="button"
          className="btn btn-default btn-outline-dark"
          data-dismiss="modal"
          onClick={formModalClose}
        >
          {i18n.t("close_modal")}
        </button>
        {!readOnly && (
          <button type="submit" className="btn btn-primary">
            {i18n.t("modal_save")}
          </button>
        )}
      </div>
    </form>
  );
};

Form.propTypes = {
  configurationSettings: PropTypes.object,
};

export default Form;
