import { createUseStyles } from "react-jss";
import classNames from "classnames";

import toolsSkin from "../skin/datagrid/tools.skin";
import {
  getFiltersTranslation,
  getTitleTranslation,
} from "../../../utils/translations-helper";
import { formActions } from "../../../actions/form-actions";
import i18n from "../../../utils/locale/i18n";
import ButtonDropdown from "../buttons/button-dropdown";
import { DROPDOWN_DIRECTION } from "../../../constants/dropdown";
import { excelExportUrl, pdfExportUrl, printUrl } from "../../../utils/urls";
import Button from "../buttons/button";
import { useDispatch, useSelector } from "react-redux";
import { columnsActions } from "../../../actions/columns-actions";
import FatalErrorMessage from "../../../components/common/error/fatal-error-message";
import MultipleButtons from "../../../components/datagrid/button/MultipleButtons";

const useStyles = createUseStyles(toolsSkin);

const onColumnsModalOpen = (dispatch) =>
  dispatch({
    type: columnsActions.MODAL_OPEN,
  });

const DatagridTools = (props) => {
  const {
    apiUrl,
    columnSearchValues,
    extendedSearchData,
    hasAdd,
    onAdd,
    onClearCache,
    onClearFiltering,
    onFilteringModalOpen,
    onOrderingReset,
    onRefresh,
    sorting,
    sortingFor,
    urlUniqueHash,
    visibleColumnsAsShortString,
    isMobileDevice,
  } = props;

  const classes = useStyles(props);
  const dispatch = useDispatch();

  const urlHistory = useSelector((state) => state.configuration.urlHistory);
  const subject = useSelector((state) => state.configuration.subject);
  const landingPageUrl = useSelector(
    (state) => state.configuration.landingPageUrl
  );

  const hasSettings = useSelector((state) => state.configuration.hasSettings);
  const hasPrint = useSelector((state) => state.configuration.hasPrint);
  const hasFilters = useSelector((state) => state.configuration.hasFilters);
  const hasColumnsButton = useSelector(
    (state) => state.configuration.hasColumnsButton
  );
  const hasExportData = useSelector(
    (state) => state.configuration.hasExportData
  );
  const hasExportPdf = useSelector((state) => state.configuration.hasExportPdf);
  const hasExportExcel = useSelector(
    (state) => state.configuration.hasExportExcel
  );

  const hasTopLeftButtons = useSelector(
    (state) => state.configuration.hasTopLeftButtons
  );
  const hasTopRightButtons = useSelector(
    (state) => state.configuration.hasTopRightButtons
  );

  const datagridButtonsTopLeft = useSelector(
    (state) => state.configuration.datagridButtonsTopLeft
  );

  const datagridButtonsTopRight = useSelector(
    (state) => state.configuration.datagridButtonsTopRight
  );

  const hasTopButtons = hasTopLeftButtons || hasTopRightButtons;
  // TODO: To pass the urlUniqueHash at the future
  const uniqueHashPrefix = urlUniqueHash ? `/${urlUniqueHash}` : "";

  const dataForExport = {
    apiUrl,
    columnSearchValues,
    sorting,
    sortingFor,
    visibleColumnsAsShortString,
    extendedSearchData,
  };

  try {
    return hasTopButtons ? (
      <div
        className={classNames({
          [classes["datagrid-tools"]]: true,
          [classes["datagrid-tools-mobile"]]: isMobileDevice,
        })}
      >
        <div>
          {hasTopLeftButtons && (
            <>
              {hasAdd && (
                <Button
                  link={true}
                  bottomSpace={true}
                  href={
                    urlHistory ? `${landingPageUrl}${uniqueHashPrefix}/add` : ""
                  }
                  icon="plus"
                  label={getTitleTranslation(formActions.ADD, subject)}
                  onClick={(event) => {
                    event.preventDefault();
                    onAdd();
                  }}
                />
              )}
            </>
          )}
          <MultipleButtons
            Button={Button}
            buttons={datagridButtonsTopLeft}
            bottomSpace={true}
          />
        </div>
        <div>
          {hasTopRightButtons && (
            <>
              <MultipleButtons
                Button={Button}
                buttons={datagridButtonsTopRight}
                bottomSpace={true}
              />
              {hasFilters && (
                <Button
                  className={
                    extendedSearchData.length > 0
                      ? classes["success-button"]
                      : classes["simple-button"]
                  }
                  onClick={onFilteringModalOpen}
                  icon={"filter"}
                  label={getFiltersTranslation(extendedSearchData.length)}
                />
              )}
              {hasFilters &&
                (extendedSearchData.length > 0 ||
                  Object.keys(columnSearchValues).length > 0) && (
                  <Button
                    className={classes["warning-button"]}
                    onClick={onClearFiltering}
                    icon="eraser"
                    label={i18n.t("filtering_remove_filters")}
                  />
                )}
              {hasColumnsButton && (
                <Button
                  onClick={() => onColumnsModalOpen(dispatch)}
                  label={i18n.t("columns")}
                  icon="list-alt"
                  bottomSpace={true}
                />
              )}
              {hasPrint && (
                <Button
                  link={true}
                  newTab={true}
                  href={printUrl(dataForExport)}
                  icon="print"
                  label={i18n.t("print")}
                  bottomSpace={true}
                />
              )}
              {hasExportData && (
                <ButtonDropdown
                  buttons={[
                    hasExportExcel && {
                      icon: "file-excel",
                      text: "Excel",
                      url: excelExportUrl(dataForExport),
                      newTab: true,
                      key: "excel",
                    },
                    hasExportPdf && {
                      icon: "file-pdf",
                      text: "PDF",
                      url: pdfExportUrl(dataForExport),
                      newTab: true,
                      key: "pdf",
                    },
                  ]}
                  buttonLabel={i18n.t("export_to_file")}
                  buttonIcon="download"
                  leftSpace={true}
                  bottomSpace={true}
                />
              )}
              {hasSettings && (
                <ButtonDropdown
                  leftSpace={true}
                  bottomSpace={true}
                  direction={DROPDOWN_DIRECTION.RIGHT}
                  buttons={[
                    {
                      icon: "broom",
                      text: i18n.t("clear_cache"),
                      onClick: onClearCache,
                      key: "clear_cache",
                    },
                    {
                      icon: "eraser",
                      text: i18n.t("clear_filtering"),
                      onClick: onClearFiltering,
                      key: "clear_filtering",
                    },
                    {
                      icon: "unlink",
                      text: i18n.t("reset_ordering"),
                      onClick: onOrderingReset,
                      key: "reset_ordering",
                    },
                    {
                      icon: "sync-alt",
                      text: i18n.t("refresh"),
                      onClick: onRefresh,
                      key: "refresh",
                    },
                  ]}
                  buttonLabel={i18n.t("settings")}
                  buttonIcon="cog"
                />
              )}
            </>
          )}
        </div>
      </div>
    ) : null;
  } catch (error) {
    console.log(error);
    return <FatalErrorMessage />;
  }
};

export default DatagridTools;
